<template>
  <div class="tax-page secure">
    <h4>安全中心</h4>
    <div class="content">
      <el-form ref="inputForm" :model="user" :rules="rules" label-width="120px">
        <el-form-item label="AppId" prop="appId">
          <el-input disabled v-model="user.appId" placeholder="请输入AppId" readonly></el-input>
          <div class="form-tip">开发者ID是公众号开发识别码，配合开发者密码可调用公众号的接口能力。</div>
        </el-form-item>
        <el-form-item label="AppSecret" prop="appSecret">
          <el-input disabled v-model="user.appSecret" placeholder="请输入AppSecret" readonly></el-input>
          <div class="form-tip">开发者密码是校验公众号开发者身份的密码，具有极高的安全性。切记勿把密码直接交给第三方开发者或直接存储在代码中。如需第三方代开发公众号，请使用授权方式接入。</div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import UserApi from '@/api/manage/user';
  export default {
    name: "secure",
    data() {
      return {
        user: {},
        rules: {
          name: [
            {required: true, message: '请输入帐号名称', trigger: 'blur'},
          ],
        }
      }
    },
    mounted() {
      this.$emit('layout:load', "secure")
      this.loadUser()
    },
    methods: {
      loadUser: function () {
        UserApi.info().then(res => {
          this.user = res.obj;
        })
      },
      onSubmit: function () {
        this.$refs['inputForm'].validate(res => {
          UserApi.saveApp(this.user).then(res => {
            this.$notify.success("重置成功!");
            this.loadUser();
          });
        });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../ui/common/stylus/variable.styl"
</style>